import React, { useMemo, useState } from 'react'

import { useUsers } from '../../store/users'
import { useNavigate } from 'react-router-dom'
import { useIsAuth } from '../../store/isAuth'
import Stars from '../stars/Stars'
import { getPromoPrice } from '../../utils/promo'
import cl from './ProductCard.module.css'
import { useCatalogs } from '../../store/catalogs'

const ProductCard = ({ product }) => {
    const { users, toggleFavouriteProduct, addToBasket } = useUsers()
    const { isAuthUser, isAuth } = useIsAuth()
    const navigate = useNavigate()
    const catalogs = useCatalogs(state => state.catalogs)
    const [animation, setAnimation] = useState(false)
    const catalog = useMemo(() => {
        return catalogs.find(catalog => catalog.name === product.catalog)
    }, [product])

    const lorem = 'lorLorem ipsum, dolor sit amet consectetur adipisicing elit. Natus suscipit cumque neque adipisci amet repellendus, harum qui ex explicabo autem modi esse dignissimos odit facilis maiores rem deserunt in nihil.em'

    const user = useMemo(() => users.find(user => {
        return user.userName === isAuthUser.userName
    }), [users, isAuthUser])

    const favourite = useMemo(() => {
        return user ? user.favourite.includes(product.name) : false
    }, [users])

    const favouriteProduct = event => {
        event.stopPropagation()
        isAuth ? toggleFavouriteProduct(user.userName, product.name) : alert('       ')
    }

    const addBasket = event => {
        event.stopPropagation()
        if (isAuth) {
            setAnimation(true)
            addToBasket(product.name, user.userName)
            setTimeout(() => { setAnimation(false) }, 2000)

        }
        else alert('       ')
    }

    const rate = useMemo(() => {
        return product.comments.reduce((sum, cur) => sum + cur.rate, 0) / product.comments.length
    }, [])

    return (
        <section onClick={() => navigate(`/catalog/${catalog.imgPath}/productPage/${product.imgPath}`)}>
            <div className={cl.image}>
                <img className={cl.bgImage} src={`./assets/products/${product.imgPath}.png`} alt={product.name} />
                {favourite ?
                    <img className={cl.icon} src="./assets/icons/favouriteHover.svg" alt="favouriteHover" onClick={favouriteProduct} /> :
                    <img className={cl.icon} src="./assets/icons/favourite.svg" alt="favourite" onClick={favouriteProduct} />}
                {!!product.promo && <div className={cl.promoOnImage}>{product.promo}%</div>}
            </div>
            <div className={cl.descr}>
                <div className={cl.descrTitle}>
                    {!!product.promo &&
                        <div className={cl.promoPrice}>
                            <div className={cl.title}>{getPromoPrice(product.price, product.promo)} BYN</div>
                            <div className={cl.subtitle}> </div>
                        </div>}
                    <div className={cl.price}>
                        <div className={cl.title}>{product.price} BYN</div>
                        <div className={cl.subtitle}></div>
                    </div>
                </div>
                <div className={cl.descrMiddle}>
                    <div className={cl.subtitle}>{product.description || lorem.substring(0, 50)}</div>
                    <Stars rate={rate} />
                </div>
                {animation ?
                    <button className={cl.addProduct} onClick={event => event.stopPropagation()}> </button> :
                    <button onClick={addBasket}> </button>
                }
            </div>
        </section>
    )
}

export default ProductCard